.product-card {
    min-height: 250px;

}

.product-card-list {
    display: flex;
    flex-direction: row;
    gap: 5px;
    position: relative;
    top: 60px;
}

.product-card-title {
    padding: 10px 60px 10px 10px;
    color: black;
    background: white;
    position: relative;
    right: 25px;
    bottom: 25px;
    text-align: center;
}

.product-card-title:hover {
    cursor: pointer;
    padding-right: 80px;
    background: rgb(209, 202, 202);
}

.product-card-description {
    width: 50%;
    background-color: #222;
    color: white;
    padding: 30px 60px 30px 30px;
    opacity: 0.85;
}

.product-card-video {
    position: relative;
}
